/* Card Styles */
.card  {
    background: rgba(85, 85, 85, 1) !important;
    /* Card Image Styles */
    img {
        border-bottom: 5px ridge rgba(182, 149, 87, 0.5);
        border: 2.5px ridge rgba(182, 149, 87, 0.5);
        object-fit: contain !important;
        max-width: calc(100% - 14px);
        padding: 5px;
    }
    /* Card Hover Styles */
    &:hover {
        background-color: rgba(65, 65, 65, 1) !important;
        /**/
        & h3 {
            font-weight: 900 !important;
        }
    }
  }