/* Navigation bar styles */
/* Basic Navigation bar styles */
.navigation {
    background: rgba(33, 33, 33, 1) !important;
    min-height: $--navigation-height !important;
    max-height: $--navigation-height !important;
    margin: 0;
    /* Brand */
    /* Area */
    .brand {
        max-width: max-content;
    }
    /* */
    .appbar-brand {
        /* Text */
        &:hover {
            color: rgba(255, 255, 255, 1);
            cursor: pointer;
        }
        /* Image */
        img  {
            border-radius: 50%;
            padding: 5px;
            height: 40px;
            width: 40px;
        }
    }
  }