/* Headings: 1 - 6 */
/* H0: Other */
h0 {
  border-bottom: 2px double rgba(171, 150, 111, 1);
  border-top: 1px double rgba(171, 150, 111, 1);
  color: rgba(255, 255, 255, 1);
  font-weight: 900 !important;
  padding: 0 0 0 0 !important;
  margin: 0 0 0 0 !important;
  text-transform: uppercase;
  text-align: center;
  /* Brand name */
  &.h0 {
    text-shadow: 1px 11px 6px rgba(16, 16, 16, 0.4),
      1px 12px 10px rgba(16, 16, 16, 0.2), 1px 13px 35px rgba(16, 16, 16, 0.4),
      1px 14px 60px rgba(16, 16, 16, 0.6);
    text-align: left;
    border: none;
  }
  /**/
  //&.h1 {}
  /**/
  //&.h2 {}
  /**/
  //&.h3 {}
  /**/
  //&.h4 {}
  /**/
  //&.h5 {}
  /**/
  //&.h6 {}
  /**/
  //&.h7 {}
  /**/
  //&.h8 {}
  /**/
  //&.h9 {}
  /**/
  //&.h10 {}
}
/* H1: Headings */
h1 {
  text-shadow: 1px 1px 1px rgba(145, 145, 145, 1),
    1px 2px 1px rgba(145, 145, 145, 1), 1px 11px 6px rgba(16, 16, 16, 0.4),
    1px 12px 10px rgba(16, 16, 16, 0.2), 1px 13px 35px rgba(16, 16, 16, 0.4),
    1px 14px 60px rgba(16, 16, 16, 0.6);
  border-bottom: 3px ridge rgba(171, 150, 111, 0.75);
  border-top: 3px ridge rgba(171, 150, 111, 1);
  color: rgba(255, 255, 255, 1);
  padding: 5px 0 5px 0 !important;
  margin: 15px 0 !important;
  text-transform: uppercase;
  -webkit-user-select: none;
  -ms-user-select: none;
  text-align: center;
  font-weight: bold;
  user-select: none;
  cursor: default;
  width: 100%;
  /* Picture Heading */
  &.h0 {
    text-shadow: 1px 1px 1px rgba(201, 173, 82, 1),
      1px 2px 1px rgba(201, 173, 82, 1), 1px 3px 1px rgb(201, 173, 82, 1),
      1px 4px 1px rgba(201, 173, 82, 1), 1px 5px 1px rgba(201, 173, 82, 1),
      1px 4px 6px rgba(139, 101, 8, 0.4), 1px 12px 10px rgba(139, 101, 8, 0.2),
      1px 13px 35px rgba(139, 101, 8, 0.2), 1px 14px 60px rgba(139, 101, 8, 0.4),
      1px 15px 60px rgba(139, 101, 8, 0.6);
    line-height: calc(360px - $--navigation-height - $--top-height);
    color: rgba(205, 149, 12, 1) !important;
    font-size: max(calc(100vw / 20), 26px);
    top: calc($--navigation-height);
    margin: 0 !important;
    position: absolute;
    font-weight: 900;
    border: none;
  }
  /* Heading */
  &.h1 {
    font-size: max(calc(100vw / 40), 24px);
    border-top: none !important;
    text-transform: uppercase;
    font-weight: 900;
  }
  /* Heading */
  &.h2 {
    font-size: max(calc(100vw / 60), 22px);
    font-weight: 600;
    /**/
    .left > & {
      padding-left: 5vw !important;
      text-align: left;
    }
    /**/
    .right > & {
      padding-right: 5vw !important;
      text-align: right;
    }
  }
  /**/
  &.h3 {
    font-size: max(calc(100vw / 80), 20px);
    padding-left: 6vw !important;
    font-weight: 600;
    text-align: left;
    /**/
    .left > & {
      padding-left: 5vw !important;
      text-align: left;
    }
    /**/
    .right > & {
      padding-right: 5vw !important;
      text-align: right;
    }
  }
  /**/
  &.h4 {
    font-size: max(calc(100vw / 100), 19px);
    padding-left: 7vw !important;
    font-weight: 600;
    text-align: left;
    /**/
    .left > & {
      padding-left: 5vw !important;
      text-align: left;
    }
    /**/
    .right > & {
      padding-right: 5vw !important;
      text-align: right;
    }
  }
  /**/
  &.h5 {
    font-size: max(calc(100vw / 120), 18px);
    padding-left: 8vw !important;
    font-weight: 600;
    text-align: left;
    /**/
    .left > & {
      padding-left: 5vw !important;
      text-align: left;
    }
    /**/
    .right > & {
      padding-right: 5vw !important;
      text-align: right;
    }
  }
  /**/
  //&.h6 {}
  /**/
  //&.h7 {}
  /**/
  //&.h8 {}
  /**/
  //&.h9 {}
  /**/
  //&.h10 {}
}
/* H2: Text */
h2 {
  color: rgba(255, 255, 255, 1);
  margin: 0 5vw 0 5vw !important;
  text-align: center;
  font-weight: 900;
  width: 100%;
  /**/
  &.h1 {
    font-size: max(calc(100vw / 120), 16px);
  }
  /**/
  &.h2 {
    font-size: max(calc(100vw / 130), 14px);
  }
  /**/
  &.h3 {
    font-size: max(calc(100vw / 140), 12px);
  }
  /**/
  &.h4 {
    font-size: max(calc(100vw / 150), 10px);
  }
  /**/
  //&.h5 {}
  /**/
  //&.h6 {}
  /**/
  //&.h7 {}
  /**/
  //&.h8 {}
  /**/
  //&.h9 {}
  /**/
  //&.h10 {}
}
/* H3: Card */
h3 {
  border-bottom: 2.5px ridge rgba(182, 149, 87, 0.5);
  border-top: 5px ridge rgba(182, 149, 87, 0.5);
  color: rgba(255, 255, 255, 1);
  margin-bottom: 10px;
  text-align: center;
  font-weight: bold;
  margin-top: 10px;
  /**/
  //&.h0 {}
  /* Heading */
  &.h1 {
    font-size: max(calc(100vw / 120), 12px);
    text-transform: capitalize;
    width: 100%;
  }
  /**/
  //&.h2 {}
  /**/
