}
/* H4: Link text */
h4 {
  /**/
  //&.h0 {}
  /* Navigation and footer bars */
  &.h1 {
    font-size: max(calc(100vw / 160), 10px);
    color: rgba(133, 133, 133, 1);
    text-transform: uppercase;
    align-self: center;
    text-align: center;
    font-weight: 900;
    cursor: pointer;
    margin: 0 15px;
    /* Link Hover */
    &:hover {
      color: rgba(255, 255, 255, 1);
    }
    /* Active Page Link */
    a.active & {
      color: rgba(255, 255, 255, 1);
    }
    /* Link Left */
    &.left {
      padding-right: 10px !important;
      text-align: left;
    }
    /* Link Right */
    &.right {
      padding-right: 10px;
      text-align: right;
    }
  }
  /**/
  //&.h2 {}
  /**/
  //&.h3 {}
  /**/
  //&.h4 {}
  /**/
  //&.h5 {}
  /**/
  //&.h6 {}
  /**/
