/* Page Styles */
/* Basic Footer bar styles */
.page {
  justify-content: center;
  align-items: center;
  flex-wrap: nowrap;
  display: flex;
  width: 100%;
  &-column {
    @extend .page;
    flex-direction: column;
  }
  &-row {
    @extend .page;
    flex-direction: row;
    &-start {
      @extend .page-row;
      justify-content: flex-start;
    }
    /**/
    .img-page-row {
      border: 2.5px ridge rgba(171, 150, 111, 1);
      object-position: 20% 100%;
      border-radius: 10px;
      object-fit: cover;
      width: 50%;
    }
    /**/
    &.left > img {
      margin: 0 5vw 0 0 !important;
      @extend .img-page-row;
    }
    /**/
    &.right > img {
      margin: 0 0 0 5vw !important;
      @extend .img-page-row;
    }
  }
}
