/* CSS Variables */
/* Variables */
/* Site Height */
$--navigation-height: 50px;
$--footer-height: $--navigation-height;
$--top-height: 0px;
$--bottom-height: 0px;
/* Background */
$--bg-darker-1: 0.7;
$--bg-darker-2: 0.9;
/* Text */
$--font-default: 12px;
/* Root defaults */
.css-hyum1k-MuiToolbar-root {
  min-height: auto !important;
  padding: 0 !important;
}
/* HMTL element */
html {
  background-image: url("../assets/image/background/site-bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  /* Body element */
  body {
    background: linear-gradient(
        rgba(0, 0, 0, $--bg-darker-1),
        rgba(0, 0, 0, $--bg-darker-1)
      ),
      rgba(65, 65, 65, 0.65);
    justify-content: center;
    min-width: 400px;
    margin: 0;
  }
  /* Upper body element. Content area + Left and Right columns */
  .upperBody {
    border-right: 1.5px double rgb(155, 155, 155);
    border-left: 1.5px double rgb(155, 155, 155);
    background: rgba(65, 65, 65, 0.35);
    min-height: calc(
      100vh - $--navigation-height - $--top-height - $--bottom-height -
        $--footer-height
    );
    max-width: 1920px;
    padding: 0;
  }
  /* Content area */
  .body {
    justify-content: flex-start;
    flex-direction: column;
    align-items: center;
    flex-wrap: nowrap;
    display: flex;
    width: 100%;
    /* Page Header */
    img.header {
      height: calc(360px - $--navigation-height - $--top-height);
      border: 5px ridge rgba(171, 150, 111, 1);
      object-position: 20% 100%;
      margin: 0 0 -17px 0 !important;
      object-fit: cover;
      border-radius: 0;
      width: 100%;
    }
  }
}
/* Horizontal line styles */
hr {
  border: 1px inset rgba(171, 150, 111, 0.5);
  margin: 5vw;
  width: 100%;
  /* Make horizontal line invisible */
  &.space {
    margin: 2.5vw;
    opacity: 0;
  }
  /* Make horizontal line as appbar link mediator */
  &.mediator {
    width: 100%;
    opacity: 0;
    margin: 0;
  }
}
